import $ from 'jquery';

import navText from './home/navText';

/* Utils */
import countering from './utils/countering';

export default function sliders() {
  const counter = `
      <div class="slider__counter">
        <div class="slider__counter__cur"></div>
        <div class="slider__counter__line"></div>
        <div class="slider__counter__length"></div>
      </div>
  `;

  $('.main__screen').find('.owl-carousel').owlCarousel({
    items: 1,
    mouseDrag: ww < 767,
    touchDrag: ww < 767,
    animateOut: !ww < 767 ? 'fadeOut' : false,
    dots: true,
    smartSpeed: 400,
    nav: true,
    navText: navText(),
    onInitialized: function(event) {
      countering(event, '.slider');
    },
    onTranslate: function(event) {
      const $this = $(event.currentTarget);
      const $parent = $this.parents('.slider');

      countering(event, '.slider');
      $this.find('.owl-prev, .owl-next').unbind();

      if (!$this.parents('.slider').find('.slider__dots').hasClass('links')) {
        const $dot = $parent.find('.slider__dot').eq(event.page.index);
        $dot.parent().siblings().find('.slider__dot').removeClass('active');
        $dot.addClass('active');
      }
    },
    onTranslated: function(event) {
      const $this = $(event.currentTarget);
      const prev = () => {
        $this.trigger('prev.owl.carousel');
      };
      const next = () => {
        $this.trigger('next.owl.carousel');
      };
      $this.find('.owl-prev').click(prev);
      $this.find('.owl-next').click(next);
    },
  });

  const $owlObjectsSl = $('.main__object__slider');

  $owlObjectsSl.each(function() {
    const $this = $(this);

    const src = (e) => {
      ;
      var url = $this.find('.main__object__slider__item').eq(e).attr('style');
      url = url.replace('background-image: url(', '').replace(');', '');
      return url;
    };


    $this.find('.owl-dot').each(function(e) {
      const $this = $(this);
      $this.css({
        backgroundImage: `url(${src(e)})`,
      });
    });
  });
  var canClick = 1;

  $('.certificates__slider').owlCarousel({
    dots: true,
    items: 1,
  });
  $('.new__slider, .production__lab-slider, .shaped-slider').owlCarousel({
    items: 1,
    nav: true,
    dots: false,
    navText: navText(),
    mouseDrag: false,
    // touchDrag: false,
    animateOut: 'fadeOut',
    onInitialized: function(e) {
      const $this = $(e.currentTarget);
      const $item = $this.find('.new__slider__item');
      var $bot;
      if ($this.hasClass('new__slider')) {
        $bot = $item.find('.new__slider__item__bot');
      } else {
        $bot = $this;
      }
      $bot.prepend(counter);
      countering(e);
    },
    onTranslate: function(e) {
      countering(e);
    },
  });

  $('.production__slider').find('.owl-carousel').owlCarousel({
    nav: true,
    dots: true,
    navText: navText(),
    items: 1,
    margin: 0,
    onTranslate: function(e) {
      const $this = $(e.currentTarget);
      const $parent = $this.parents('.production__slider');

      const $dot = $parent.find('.slider__dot');
      $dot.removeClass('active');
      $dot.eq(e.page.index).addClass('active');
      countering(e, '.slider');
    },
    onInitialized: function(e) {
      countering(e, '.slider');
    },
  });
  $('.production-materials-slider').owlCarousel({
    nav: true,
    dots: false,
    navText: navText(),
    items: 1,
    margin: 0,
    mouseDrag: false,
    animateOut: 'fadeOut',
  });

  $('.owl-carousel').owlCarousel({
    items: 1,
    nav: true,
    dots: false,
    navText: navText(),
    margin: 20,
  });


  $('.slider').each(function() {
    const $this = $(this);
    const $dot = $this.find('.slider__dot');
    var $owlDot = $this.children('.owl-carousel').children('.owl-dots').find('.owl-dot');
    if ($owlDot.length <= 0) {
      $owlDot = $this.find('.owl-dot');
    }


    $dot.eq(0).addClass('active');
    $dot.on('click', function() {
      const $this = $(this);
      if (!$this.parents('.slider__dots').hasClass('links')) {
        const i = $dot.index(this);
        if ($this.parents('.main__screen').length > 0) {
          if (!canClick) return;
        }

        canClick = 0;
        $owlDot.eq(i).click();
        setTimeout(() => {
          canClick = 1;
        }, 400 * 2);
        $this.parents('.slider__dots').find('.slider__dot').removeClass('active');
        $this.addClass('active');
      }
    });
  });
}
