import $ from 'jquery';
import { TimelineMax } from 'gsap';

export default function(options) {

  var settings = {
    element: '.animate-clip',
    time: 1,
    minusTime: '-=0',
  };
  settings = Object.assign(settings, options);
  const tl = new TimelineMax();

  const width = $(settings.element).innerWidth();
  const height = $(settings.element).innerHeight();

  const mb_clip0 = `rect(0px 0px ${height}px 0px)`;
  const mb_clip1 = `rect(0px ${width}px ${height}px 0px)`;

  tl
    .fromTo(settings.element, settings.time, {
      opacity: 0,
      clip: mb_clip0,
    }, {
      opacity: 1,
      clip: mb_clip1,
    }, settings.minusTime)
    .fromTo($(settings.element).find('.animate-clip-inner'), settings.time, {
      opacity: 0,
      y: 40,
    }, {
      opacity: 1,
      y: 0,
      onComplete: function() {
        $(settings.element).removeAttr('style');
        $(settings.element).find('.animate-clip-inner').removeAttr('style');
      },
    }, '-=' + settings.time);
}
