import $ from 'jquery';

export default function video() {
  $('.video').on('click', function(e) {
    const $this = $(this);
    const $video = $this.find('video')[0];

    if (!$('.page__hero__play, .page__hero__play *, .video__play, .video__play *').is(e.target)) {
      if ($this.hasClass('play')) {
        $video.pause();
        $this.removeClass('play');
      }
    }
  });
  $('.video__play').on('click', function(e) {
    const $this = $(this);
    const $parent = $this.parents('.video');
    const $video = $parent.find('video')[0];
    $parent.toggleClass('play');
    if ($parent.hasClass('play')) {
      $video.play();
    } else {
      $video.pause();
    }
  });
  $('.page__hero__play').on('click', function(e) {
    const $this = $(this);
    const $play = $this.find('.video__play');
    if (!$('.video__play').is(e.target)) {
      $play.click();
    }
  });
}
