import $ from 'jquery';

import animateClip from './animateClip';
import mainBotProductionSlider from './home/mainBotProductionSlider';

export default function mainScreenSliding(count = 0) {
  const tl = new TimelineMax();
  const $mainNewsSliderNextBorder = $('.mainNewsSliderNextBorder');

  var prevCount;
  var servicesHasBeenActive = 0;
  const sliding = (count, fromRect = `rect( ${wh}px 0px ${wh}px 0px)`) => {

    if ($('.main__screen').length === 0) return;

    $('body, html').scrollTop(0);

    var toRect;
    toRect = `rect(0px ${ww}px ${wh}px 0px)`;
    const $screen = $('.main__screen').eq(count);
    $screen.addClass('active');

    tl.fromTo($screen, 0.4, {
      clip: fromRect,
    }, {
      clip: toRect,
      onStart: function() {
        $screen.css({
          opacity: 1,
          visibility: 'visible',
          zIndex: 2,
        });
        $screen.siblings().css({
          zIndex: 1,
        });
      },
      onComplete: function() {
        $screen.siblings().css({
          opacity: 0,
          visibility: 'hidden',
        });
        $screen.css({
          clip: 'auto',
        });
      },
    });
    var time;
    if (count > prevCount) {
      time = 200;
    } else {
      time = 0;
    }
    if (count === 1) {
      setTimeout(function() {
        $('.header').addClass('in-light').removeClass('in-dark');
      }, time);
    } else {
      setTimeout(function() {
        $('.header').addClass('in-dark').removeClass('in-light');
      }, time);
    }

    const { hash } = location;

    if (hash === '#services' && !servicesHasBeenActive) {
      setTimeout(() => {
        animateClip({
          element: '.owl-item.active .service__info',
          minusTime: '+=0',
        });
        servicesHasBeenActive = 1;
      }, 10);
    }

    const $mainNewsSlider = $('.mainNewsSlider');

    if (hash === '#news') {
      $mainNewsSliderNextBorder.addClass('animate');
      $mainNewsSlider.trigger('play.owl.autoplay');
    } else {
      $mainNewsSliderNextBorder.removeClass('animate');
      $mainNewsSlider.trigger('stop.owl.autoplay');
    }
    prevCount = count;
  };

  var prevHash = '';
  const checkHash = () => {
    const { hash } = location;
    let id = null;
    let fromRect = null;

    if (hash.length <= 0) {
      count = 0;
      sliding(count);
    }

    if (prevHash === hash) return;

    $('.main__screen').each(function(e) {
      const cur_id = '#' + $(this).attr('id');
      if (cur_id === hash) {
        id = cur_id;
        count = e;
      }
    });

    if (count < prevCount) {
      fromRect = 'rect( 0px 0px 0px 0px)';
    } else {
      fromRect = `rect( ${wh}px 0px ${wh}px 0px)`;
    }

    if (id === hash) {
      sliding(count, fromRect);
    }

    if (hash === '#home') {
      setTimeout(mainBotProductionSlider, 100);
    }

    prevHash = hash;
    prevCount = count;
  };

  checkHash();

  $('a').on('click', function() {
    const $this = $(this);
    const href = $this.attr('href');

    if (href === '#') {
      return false;
    } else if (href.indexOf('#') > -1) {
      const top = $(href).offset().top;
      $('html,body').animate({
        scrollTop: top,
      });
    }
  });

  window.addEventListener('popstate', function(event) {
    checkHash();
  });

  $('.site-nav__next').on('click', function() {
    const $this = $(this);

    if ($this.hasClass('scroll')) {
      const $hero = $this.parents('.page__hero');
      const h = $hero.innerHeight();


      $('html, body').animate({
        scrollTop: h,
      }, 400);
      return false;
    }
  });

  $('.header__logo').on('click', function() {
    if (window.location.hash === '' && $('.main__screens').length > 0) {
      return false;
    }
  });

  var wheelCount = 0;
  var timer;
  let isCanWheel = true;

  $(window).on('mousewheel wheel', function(e) {
    if (ww > 767 && $('.main__screens').length > 0) {
      const delta = e.originalEvent.deltaY;
      wheelCount++;

      if (wheelCount > 2 && isCanWheel) {
        isCanWheel = false;

        const $mainScreen = $('.main__screen');
        const mainScreenLength = $mainScreen.length - 1;

        if (delta > 0) {
          count += 1;
        } else {
          count -= 1;
        }

        count = Math.max(0, Math.min(count, mainScreenLength));
        const hash = '#' + $mainScreen.eq(count).attr('id');
        window.location.hash = hash;
        wheelCount = 0;

        /* If user try scroll when already changed slide, then allow scroll for him */
        setTimeout(() => {
          if (wheelCount > 0) {
            isCanWheel = true;
          }
        }, 1600);
      }


      /* If user don't stop scroll not allow scroll for him */
      if (timer) {
        window.clearTimeout(timer);
      }

      /* Allow scroll again if user stop scroll */
      timer = window.setTimeout(function() {
        wheelCount = 0;
        isCanWheel = true;
      }, 600);
    }
  });
}
