import $ from 'jquery';
import { TimelineMax } from 'gsap';

export default function tabs() {
  const tl = new TimelineMax();
  $('.tabs__dot').on('click', function() {
    const $this = $(this);
    const i = $('.tabs__dot').index(this);
    const $parent = $this.parents('.tabs');
    const $items = $parent.find('.tabs__items');
    const $item = $parent.find('.tabs__item').eq(i);
    const fromH = $parent.find('.tabs__item').innerHeight();

    $this.parents('.tabs').find('.tabs__dot').removeClass('active');
    $this.addClass('active');

    tl.to($item.siblings(), 0.4, {
      opacity: 0,
      onStart: function() {
        $items.css({
          height: fromH
        });
      },
      onComplete: function() {
        $item.siblings().removeClass('active');
      }
    }).to($item, 0.4, {
      onStart: function() {
        $item.addClass('active');
      },
      onComplete: function() {
        $items.removeAttr('style');
      },
      opacity: 1,
    });
  });
}
