import $ from 'jquery';

var changeDOM = null;
export default function newResponsive() {
  const $othN = $('.other-news');
  const $next = $othN.find('.link-next');

  if (ww < 767) {
    if (changeDOM !== 0) {
      $next.appendTo($othN.find('.container'));
      changeDOM = 0;
    }
  } else {
    if (changeDOM !== 1) {
      $next.appendTo($othN.find('.section__header'));
      changeDOM = 1;
    }
  }
}
