import $ from 'jquery';
import { TimelineMax } from 'gsap';

export default function findCost() {
  var prevCounter = 0;
  var counter = 0;
  const tl = new TimelineMax();
  class FindCost {
    constructor() {
      this.item = $('.find-cost__item.active');
      this.right = this.item.find('.find-cost__item__right');
      this.panel = $('.find-cost__item').find('.panel');
      this.curPanel = this.item.find('.panel');
      this.width = this.right.width();
      this.height = this.right.height();
      this.counter = $('.find-cost').find('.slider__counter');
      this.length = $('.find-cost__item').length;

      this.area = {
        length: 25,
        width: 100,
        height: 10
      };
    }
    sliding(counter) {
      const $item = $('.find-cost__item');

      if (prevCounter !== counter) {
        tl.fromTo($item.find('.find-cost__item__left'), 0.4, {
          opacity: 1,
        }, {
          opacity: 0,
          onComplete: () => {
            $item.removeClass('active');
            $item.eq(counter).addClass('active');

            this.item = $item.eq(counter);
            this.curPanel = this.item.find('.panel');
            this.counter.find('.slider__counter__cur').text(counter+1);
          }
        });
        tl.fromTo($item.eq(counter).find('.find-cost__item__left'), 0.4, {
          opacity: 0,
        }, {
          opacity: 1,
        });
        if (counter !== 6) {
          $('.input__check-valid-message').remove();
        }

      }
      prevCounter = counter;
    }
    init() {
      $('.find-cost__item').find('.panel').removeAttr('style');
      this.item = $('.find-cost__item.active');
      this.right = this.item.find('.find-cost__item__right');
      this.panel = $('.find-cost__item').find('.panel');
      this.curPanel = this.item.find('.panel');
      this.width = this.right.width();
      this.height = this.right.height();
      this.counter = $('.find-cost').find('.slider__counter');
      this.length = $('.find-cost__item').length;

      $('.find-cost').find('.panel').css({
        width: this.width,
        height: this.height,
        transition: '.3s ease'
      });
      this.counter.find('.slider__counter__length').text(this.length);
      this.onCalculateArea();
      this.onChangeAdvances(5);
    }
    onChangeThickness(value) {
      const maxHeight = 300;
      const height =this.height/maxHeight*value;
      this.panel.css({ height });
      this.curPanel.find('.panel__value').text(value+' мм');
    }
    onChangeWidth(value) {
      const maxWidth = 1200;
      const width =this.width/maxWidth*value;
      this.panel.css({ width });
      this.curPanel.find('.panel__value').text(Number.isInteger(value/1) ? value+' мм' : value );
    }
    onChangeThicknessLayer(value, direction) {
      const thickness = value.replace(',','.')/1*10;
      this.curPanel.find('.panel__value').text(value+' мм');

      if (direction === 'top') {
        this.curPanel.find('svg.panel__top').find('path').attr('stroke-width',thickness+'px');
      }
      if (direction === 'bottom') {
        this.curPanel.find('.panel__bottom').css({height: thickness});
      }
    }
    onCalculateArea(e) {


      setTimeout(() => {
        if (e) {
          const $input = $(e.target);
          const type = $input.attr('name');
          this.area[type] = $input.val()/1 || 0;
        }

        const area = 2 * ( this.area.length + this.area.width ) * this.area.height;
        $('.find-cost__area__result').val(area).text(area);
      });
    }
    onChangeAdvances(index) {
      const $img = $('.find-cost__item__imgs').find('img');
      $img.hide();
      $img.eq(index).show();
    }
  }
  var findCost = new FindCost();

  $('.find-cost__actions').find('[type="radio"]').on('click', function() {
    const $this = $(this);
    const name = $this.attr('name');
    var value = $this.val();

    if (name === 'panel_thickness') {
      findCost.onChangeThickness(value);
    }
    if (name === 'panel_width') {
      findCost.onChangeWidth(value);
    }
    if (name === 'panel_thickness_layer_top') {
      findCost.onChangeThicknessLayer(value, 'top');
    }
    if (name === 'panel_thickness_layer_bottom') {
      findCost.onChangeThicknessLayer(value, 'bottom');
    }
  });
  const $advRadio = $('.find-cost__advances').find('[type="radio"]');
  $advRadio.on('click', function() {
    const index = $advRadio.index(this);
    findCost.onChangeAdvances(index);
  });

  $('.find-cost__actions').each(function(index) {
    const $this = $(this);
    var index_radio = -1;
    if (index === 1) {
      index_radio = 2;
    }
    $this.find('[type="radio"]').eq(index_radio).prop('checked', true);
  });
  const $inputArea = $('.find-cost__area-calculator').find('input');
  $inputArea.on('change wheel keypress keyup', function(e) {
    findCost.onCalculateArea(e);
  });
  $inputArea.focus(function() {
    $(this).select();
  });
  $('.find-cost__nav').children().on('click', function() {

    const len = $('.find-cost__item').length - 1;
    const $this = $(this);

    if ($this.attr('class').indexOf('next') > -1) {
      counter++;
    } else {
      counter--;
    }
    counter = Math.max(0, Math.min(counter,len));
    findCost.sliding(counter);
  });

  counter = 0;
  findCost.init();
  findCost.sliding(counter);

  $(window).on('resize', function() {
    findCost.init();
  });
}
