import $ from 'jquery';
import { TimelineMax } from 'gsap';

export default () => {
  const $openGallery = $('.openGallery');
  const timeLineMax = new TimelineMax();
  const time = 0.3;

  $openGallery.on('click', function() {
    const index = $openGallery.index(this);
    const $currentGallery =
      $('.gallery')
        .find('.customSlider-wrapper')
        .eq(index);

    $currentGallery.addClass('active');

    timeLineMax
      .fromTo($currentGallery, time, {
        clip: `rect(0px ${ww}px 0px ${ww}px)`,
        opacity: 0,
      }, {
        clip: `rect(0px ${ww}px ${wh}px 0px)`,
        opacity: 1,
        onComplete: function() {
          $currentGallery
            .removeAttr('style');
        },
      });
  });

  $('.closeGallery').on('click', function() {
    const $currentGallery = $(this).parents('.customSlider-wrapper');

    timeLineMax
      .fromTo($currentGallery, time, {
        clip: `rect(0px ${ww}px ${wh}px 0px)`,
        opacity: 1,
      }, {
        clip: `rect(0px ${ww}px 0px ${ww}px)`,
        opacity: 0,
        onComplete: function() {
          $currentGallery
            .removeClass('active')
            .removeAttr('style');
        },
      });
  });
};
