import $ from 'jquery';

const resize = () => {
  const { hash, pathname } = window.location;

  if (hash === '#home' || pathname === '/' && !hash) {
    const $mainBotProductionSlider = $('.mainBotProductionSlider');

    $mainBotProductionSlider.trigger('destroy.owl.carousel');

    $mainBotProductionSlider.css({ width: '' });
    $mainBotProductionSlider.css({
      width: $mainBotProductionSlider.innerWidth(),
    });

    $mainBotProductionSlider.owlCarousel({
      dots: true,
      items: 1,
      autoplay: true,
      autoplayTimeout: 2500,
    });

    $('.big-title').css({ transform: '' });
  }
};

export default () => {
  resize();

  $(window).on('resize', function() {
    resize();
  });
};
