import $ from 'jquery';

export default (event, parents = null, isLoop = false) => {
  const $this = $(event.currentTarget);
  if ($(event.target).hasClass('main__object__slider')) {
    return false;
  }

  let index = event.item.index + 1;
  let length = event.item.count;

  if (isLoop) {
    index = event.page.index + 1;
    length = event.page.count;
  }

  var $counter = $this.find('.slider__counter');
  if (parents !== 0) {
    $counter = $this.parents(parents).find('.slider__counter');
  }

  $counter.find('.slider__counter__cur').text(index);
  $counter.find('.slider__counter__length').text(length);
};
