import $ from 'jquery';

import mainBotProductionSlider from './home/mainBotProductionSlider';

export default function loadAnimations() {
  const tl = new TimelineMax();


  const main = () => {
    const $mainBot = $('.main__bot');
    const $mainScreen = $('.main__screen');
    let timeoutToShowCounter = 0;

    if ($mainScreen.find('video').length > 0) {
      $mainScreen.find('video')[0].play();
    }
    if ($('.main__home').hasClass('active') && ww > 767) {
      timeoutToShowCounter = 1400;

      tl
        .fromTo('.header', 1, {
          opacity: 0,
          y: -$('.header').innerHeight() / 2,
        }, {
          opacity: 1,
          y: 0,
        }, '+=.5')
        .fromTo('.video__blackout', 1, {
          opacity: 0,
        }, {
          opacity: 1,
        }, '-=1')
        .fromTo('.big-title', 1, {
          opacity: 0,
          y: $('.big-title').innerHeight() / 5.225,
        }, {
          opacity: 1,
          y: 0,
        }, '-=1');

      setTimeout(() => {
        $mainBot.addClass('show');
        mainBotProductionSlider();
      }, 800);

    } else {
      $mainBot
        .css({ 'transition': '0s' })
        .addClass('show');
      mainBotProductionSlider();
    }

    setTimeout(() => {
      $('.mainSliderCounter').addClass('show');
    }, timeoutToShowCounter);
  };

  const checkLoad = () => {
    if (hbLoad) {
      main();
    } else {
      setTimeout(function() {
        checkLoad();
      }, 100);
    }
  };

  checkLoad();
}
