export const viewports = {
  large: {
    max: Infinity,
    min: 1440,
  },
  small: {
    max: 1439,
    min: 1024,
  },
  tablet: {
    max: 1023,
    min: 768,
  },
  mobile: {
    max: 767,
    min: 0,
  },
};

export function compareViewports(windowWidth) {
  const {
    large,
    small,
    tablet,
    mobile,
  } = viewports;

  return {
    isLarge: windowWidth <= large.max && windowWidth >= large.min,
    isTablet: windowWidth <= tablet.max && windowWidth >= tablet.min,
    isSmall: windowWidth <= small.max && windowWidth >= small.min,
    isMobile: windowWidth <= mobile.max && windowWidth >= mobile.min,
  };
}
