import $ from 'jquery';
import selectric from './lib/jquery.selectric.min';
import mask from './lib/jquery.maskedinput';
import owlCarousel from 'owl.carousel';

/* Modules */
import customSlider from './modules/customSlider';

import mainScreenSliding from './main-screen-sliding';

import forDevelop from './for-develop'; //delete
import expander from './lib/expander';
import fileUpload from './lib/fileupload';
import formValidation from './lib/form_validation';
import sliders from './sliders';
import responsive from './responsive';
import modals from './modals';
import tabs from './tabs';
import ymap from './ymap';
import video from './video';
import scrollParallax from './scroll-parallax';
import colorMap from './color-map';
import tabResponsive from './tabResponsive';
import sandwichResponsive from './sandwichResponsive';
import videoResponsive from './videoResponsive';
import sliderResponsive from './sliderResponsive';
import showMore from './showMore';
import findCost from './findCost';
import newResponsive from './newResponsive';
import fixObjectsMobileSlider from './utils/fixObjectsMobileSlider';
import openCloseGallery from './modules/openCloseGallery';

/* functions of pages */
import homeFunctions from './home';

const { NODE_ENV } = process.env;
global.__DEV__ = NODE_ENV === 'development';
global.__PROD__ = NODE_ENV === 'production';

const roofImg = () => {
  const $img = $('.roofing__hero__img');
  const m = $img.innerHeight() / 2;
  $img.css({
    marginBottom: -m,
  });
  $('.roofing__hero').css({
    marginBottom: m + 50,
  });
};

$(document).ready(() => {
  global.mobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
  $('.select').selectric();

  /* First */
  responsive();

  /* Other functions */
  customSlider();
  homeFunctions();
  expander();
  fileUpload();
  formValidation();
  sliders();
  mainScreenSliding(0);
  modals();
  tabs();
  ymap();
  video();
  scrollParallax();
  colorMap();
  forDevelop();
  findCost();
  showMore();
  fixObjectsMobileSlider();
  openCloseGallery();

  const allResponsives = () => {
    global.isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
    roofImg();
    tabResponsive();
    sandwichResponsive();
    videoResponsive();
    sliderResponsive();
    newResponsive();

    const $mainCatalog = $('.main__catalog');
    const $item = $mainCatalog.find('.slider__item');
    $item.removeAttr('style');

    if (ww < 768) {
      setTimeout(() => {
        const $owl = $mainCatalog.find('.owl-carousel');
        const height = $owl.innerHeight();

        $item.css({ height });
      }, 1000);
    }

  };

  allResponsives();

  $(window).on('resize', function() {
    global.ww = window.innerWidth;
    global.wh = window.innerHeight;
    allResponsives();
  });
});
