import $ from 'jquery';

export default () => {
  const $customSlider = $('.customSlider');

  const changeDotActive = (event) => {
    const $this = $(event.currentTarget);
    const $wrapper = $this.parent('.customSlider-wrapper');
    const $dots = $wrapper.find('.customSlider-dots');
    const $dotsInner = $wrapper.find('.customSlider-dotsInner');
    const $dot = $wrapper.find('.customSlider-dot');
    const { index } = event.page;

    const dotsWidth = $dots.innerWidth();
    const dotsInnerWidth = $dotsInner.innerWidth();
    const rest = dotsWidth - dotsInnerWidth;

    let x = 0;

    /* Variant #1 */
    if (dotsInnerWidth > dotsWidth) {
      const dotWidth = $dotsInner.innerWidth() / $dot.length;
      x = -(dotWidth * index) + (dotsWidth / 2) - dotWidth / 2;
      x = Math.max(Math.min(0, x), rest);
    }

    /* Variant #2 */
    // const ratio = rest / ($dot.length - 1);
    // const x = ratio * index;


    $dotsInner.css({
      transform: `translate(${x}px, 0)`,
    });


    $dot.removeClass('active');
    $dot.eq(index).addClass('active');
  };

  $customSlider.owlCarousel({
    items: 1,
    nav: true,
    dots: true,
    loop: true,
    onInitialized(event) {
      const $this = $(event.currentTarget);

      $this.parent('.customSlider-wrapper').find('.customSlider-dot').eq(0).addClass('active');
    },
    onTranslate(event) {
      changeDotActive(event);
    },
    onResized(event) {
      changeDotActive(event);
    },
  });

  $('.customSlider-wrapper').each(function() {
    const $slider = $(this);

    $(this).find('.customSlider-dot').on('click', function() {
      const $this = $(this);
      const index = $this.index();

      $slider.find('.owl-dot').eq(index).click();
    });
  });
};
