import $ from 'jquery';
import { TimelineMax } from 'gsap';

export default function showMore() {
  const tl = new TimelineMax();

  const newElement = `<a href="#" class="new showing" style="opacity: 0">
    <div class="new__pic-wrapper">
      <!--suppress CssUnknownTarget -->
    <div class="new__pic bg_found" style="background-image: url(img/news/2.jpg)"></div>
    </div>
    <div class="new__info">
      <time datetime="2018-05-18" class="new-date"> 18 мая 2018 </time>
      <h3 class="new__title h3"> ГК «ТехноСтиль» примет участие в выставке MosBuild-2018 в апреле </h3>
    </div>
  </a>`;
  $('.more-news').on('click', function() {
    const last = $('.news').length - 1;
    const $news = $('.news').eq(last);
    $news.append(newElement + newElement);
    $('html, body').animate({
      scrollTop: $('.new.showing').eq(0).offset().top,
    });
    setTimeout(() => {
      tl
        .fromTo('.new.showing', 0.4, {
          opacity: 0,
          y: 400,
        }, {
          opacity: 1,
          y: 0,
          onComplete: function() {
            $('.new.showing').removeClass('showing');
          },

        });
    }, 10);
  });
}
