import $ from 'jquery';

import loadAnimations from './loadAnimations';

export default function ymap() {
  const svgMark = 'data:image/svg+xml;charset=utf-8;base64,';

  ymaps.ready(function() {
    $('.yandex-map').each(function() {
      const ID = $(this).attr('id');
      var myMap = new ymaps.Map(ID, {
        center: [56.82748439259351, 60.60556172488401],
        region: [56.82469315878292, 60.59526204226681],
        placeMark: [56.82748439259351, 60.60556172488401],
        zoom: 17,
      }, {
        searchControlProvider: 'yandex#search',
      });
      if (mobile) {
        myMap.behaviors.disable('drag');
      }
      myMap.behaviors.disable('scrollZoom');


      myMap.geoObjects
        .add(new ymaps.Placemark([56.82748439259351, 60.60556172488401], {
          balloonContent: 'Техностиль',
        }, {
          // preset: 'islands#icon',
          // iconColor: '#f00',
          iconLayout: 'default#image',
          iconImageHref: svgMark,
          iconImageSize: [81, 88],
          iconImageOffset: [-30, -90],
        }));
    });

    loadAnimations();
  });
  $('.map__mark').on('click', function() {
    const $this = $(this);
    const t = $this.offset().top;
    const l = $this.offset().left;
    const w = $this.innerWidth();
    const h = $this.innerHeight();

    const top = t - h / 2 - 10;
    const left = l + w / 2;

    $('.modal-prompt').remove();
    const modal = `
        <div class="modal-prompt">
          <div class="modal-prompt__title"> Леруа Мерлен </div>
          <div class="modal-prompt_description"> г. Балашиха,
            <br>Московская область </div>
          <div class="close"> </div>
          <div class="modal-prompt__arrow"></div>
        </div>
    `;

    $('body').append(modal);

    var $modal = $('.modal-prompt');

    $modal.css({
      left: left,
      top: top,
    });

    const modalClimbs = $modal.offset().left + $modal.innerWidth();

    if (modalClimbs > ww || ww < 767) {
      $modal.css({
        right: 20,
        left: 20,
      });
      $modal.find('.modal-prompt__arrow').css({
        left: left - 18,
      });
    }

    setTimeout(() => {
      $modal.addClass('active');
    });

    $('.modal-prompt').find('.close').on('click', function() {
      $(this).parent().remove();
    });

  });

}
