import $ from 'jquery';

import { compareViewports } from './options/viewports';

var changeDOM = null;
export default function tabResponsive() {
  const threeDots = '<div class="tabs__dots-others__btn"><?xml version="1.0" encoding="UTF-8"?><svg viewBox="0 0 15 3" version="1.1" xmlns="http://www.w3.org/2000/svg"><circle cx="1.5" cy="1.5" r="1.5"></circle><circle cx="7.5" cy="1.5" r="1.5"></circle><circle cx="13.5" cy="1.5" r="1.5"></circle></svg></div>';
  const { isSmall, isTablet, isMobile } = compareViewports(ww);

  $('.tabs').each(function() {
    const $tabs = $(this);
    const $dot = $tabs.find('.tabs__dot ');
    const $dots = $tabs.find('.tabs__dots');
    const l = $dot.length - 1;
    const removeSelect = () => {
      $('.tab__dot-wrapper').remove();
    };
    const removeOthers = () => {
      $dot.each(function() {
        const $this = $(this);
        $this.appendTo($tabs.find('.tabs__dots'));
      });
      $('.tabs__dots-others').remove();
    };
    const addOthers = (quantity) => {
      $dot.each(function(e) {
        const $this = $(this);
        $this.appendTo($dots);
      });

      $dots.append(`<div class="tabs__dots-others">${threeDots}<div class="tabs__dots-others-inner"></div></div>`);

      $dot.each(function(e) {
        const $this = $(this);
        if (e > l - quantity) {
          $this.appendTo($tabs.find('.tabs__dots-others-inner'));
          $this.wrap(function() {
            return '<div></div>';
          });
        }
      });

      changeDOM = 0;

      $('.tabs__dots-others__btn').on('click', function() {
        $(this).siblings('.tabs__dots-others-inner').toggleClass('active');
      });
      $(document).on('click', function(e) {
        if (!$('.tabs__dots-others, .tabs__dots-others *').is(e.target)) {
          $('.tabs__dots-others-inner').removeClass('active');
        }
      });
    };

    if (isSmall) {
      if (changeDOM !== 1) {
        removeOthers();
        addOthers(2);
        removeSelect();

        changeDOM = 1;
      }
    } else if (isTablet) {
      if (changeDOM !== 2) {
        removeOthers();
        addOthers(5);
        removeSelect();

        changeDOM = 2;
      }
    } else if (isMobile) {
      if (changeDOM !== 3) {
        removeOthers();

        $dot.wrap(function() {
          return '<div class="tab__dot-wrapper"></div>';
        });

        $dots.on('click', function() {
          const $this = $(this);
          $this.toggleClass('active');
        });

        changeDOM = 3;
      }
    } else {
      if (changeDOM !== 4) {
        removeOthers();
        removeSelect();

        changeDOM = 4;
      }
    }
  });
}
