import $ from 'jquery';

export default function fileUpload() {
  const removeFile = () => {
    $('.file-upload__file__remove').on('click', function() {
      const $this = $(this);
      const $parent = $this.parents('.file-upload');
      const $input = $parent.find('.file-upload__input');
      const $file = $this.parent();
      $input.attr('type','text');
      $input.attr('type','file');
      $input.prop('files', null);
      $file.remove();

    });
  };
  $('.file-upload__input').on('change', function() {
    const files = this.files;
    const filesArr = Object.entries(files);
    const $parent = $(this).parents('.file-upload');
    const $files = $parent.find('.file-upload__files');
    const filesHtmlArr = filesArr.map(function(f) {
      return (`
        <div class="file-upload__file">
          <div class="file-upload__file__percentage">
          </div>
          <div class="file-upload__file__right">
            <div class="file-upload__file__title">
              ${f[1].name}
            </div>
            <div class="file-upload__file__remove">
            </div>
          </div>
        </div>
      `);
    });


    const filesHtml = filesHtmlArr.join('');
    $files.html(filesHtml);
    removeFile();
  });


  // FAKE UPLOAD
  var i = 0;
  const f = () => {
    if (i < 100) {
      i += 1;
      $('.file-upload__file__percentage').text(i + '%');
      console.warn('FAKE Upload!', i);
      setTimeout(function() {
        f();
      }, 50);
    }
  };
}

