import $ from 'jquery';

export default () => {
  const $owlStage = $('.main__objects .owl-stage');

  const onResize = () => {
    $owlStage.css({ height: '' });

    if (window.innerWidth < 768) {
      const height = $owlStage.innerHeight();

      setTimeout(() => {
        $owlStage.css({ height });
      }, 100);
    }
  };

  onResize();

  $(window).on('resize', onResize);
};
