import $ from 'jquery';

var changeDOM = null;

export default function sandwichResponsive() {
  $('.sandwich__objects').each(function() {
    const $this = $(this);
    const $info = $this.find('.main__objects__info');
    const $dots = $this.find('.slider__dots');

    if (ww < 1024) {
      if (changeDOM !== 0) {
        const dots_height = $dots.innerHeight();
        const dots_top = $dots.position().top;
        const top = dots_height + dots_top + 20;

        $info.css({ top });
        changeDOM = 0;
      }
    } else {
      if (changeDOM !== 1) {
        $info.removeAttr('style');
        changeDOM = 1;
      }
    }
  });
}
