import $ from 'jquery';
import { TimelineMax } from 'gsap';

export default function expander() {
  let canClick = 1;
  const tl = new TimelineMax();
  $('.expander__header').on('click', function() {
    const $this = $(this);
    const $exp = $this.parents('.expander').eq(0);
    const $c = $exp.find('.expander__content').eq(0);
    const $c_wrap = $exp.find('.expander__content-wrapper').eq(0);
    const h = $c.innerHeight();
    const time = 0.4;

    if (!canClick) return;
    canClick = 0;
    $exp.toggleClass('active');

    if (!$exp.hasClass('active')) {
      tl.fromTo($c_wrap, time, {
        height: h
      },
      {
        height: 0,
        ease: Expo.easeOut,
        onComplete: function() {
          $c_wrap.removeAttr('style');
        }
      });
      tl.fromTo($c, time, {opacity: 1}, {
        opacity: 0, ease: Expo.easeOut,
      }, '-=' + time);
    } else {
      tl.fromTo($c_wrap, time, {height: 0}, {height: h, ease: Expo.easeOut,});
      tl.fromTo($c, time, {opacity: 0}, {
        opacity: 1, ease: Expo.easeOut,
        onComplete: function() {
          $c_wrap.removeAttr('style');
        }
      }, '-=' + time);
    }

    setTimeout(() => {
      canClick = 1;
    }, time * 1000);
  });
}
