import $ from 'jquery';

import navText from './navText';
import countering from '../utils/countering';

export default () => {
  const $mainNewsSliderNextBorder = $('.mainNewsSliderNextBorder');
  const $mainNewsSliderNextImage = $('.mainNewsSliderNextImage');
  const $mainNewsSliderItem = $('.mainNewsSliderItem');

  /* Autoplay set in `main-screen-sliding.js` */

  const $owl = $('.mainNewsSlider').owlCarousel({
    items: 1,
    autoplayTimeout: 7000,
    animateOut: 'fadeOut',
    mouseDrag: ww < 767,
    nav: true,
    dots: true,
    loop: true,
    navText: navText(),
    onTranslate(event) {
      const { index, count } = event.page;
      const nextIndex = index >= count - 1
        ? 0
        : index + 1;
      const currentImage = $mainNewsSliderItem.eq(nextIndex).css('background-image');

      $mainNewsSliderNextImage.css({ backgroundImage: currentImage });

      refreshNextAnimate();
      countering(event, '.mainNews', true);
    },
  });

  $('.mainNewsSliderNext').on('click', function() {
    $owl.trigger('next.owl.carousel');
  });

  const refreshNextAnimate = () => {
    $mainNewsSliderNextBorder.removeClass('animate');

    $owl.on('changed.owl.carousel', function() {
      $owl.trigger('stop.owl.autoplay');
      $owl.trigger('play.owl.autoplay');
    });

    setTimeout(() => {
      $mainNewsSliderNextBorder.addClass('animate');
    }, 10);
  };
};
