import $ from 'jquery';
import { TimelineMax } from 'gsap';

export default function modals() {
  const $modalMenu = $('[data-layout="modalMenu"]');
  const tl = new TimelineMax;
  var canClick = 1;
  var scrollTop = 0;

  const openModal = () => {
    scrollTop = $(window).scrollTop();
    $('html, body').addClass('open-modal');
    $('body').css({ top: -scrollTop });
  };
  const hideModal = () => {
    $('html, body').removeClass('open-modal');
    $(window).scrollTop(scrollTop);
    $('body').removeAttr('style');
  };

  class Modals {
    constructor() {
      const th = this;
      this.bg = {
        show: function() {
          const bg = '.modal-background';

          tl
            .to(bg, 0.4, {
              onStart: function() {
                $(bg).addClass('active');
              },
              opacity: 1,
            });
          tl.play();
          this.state = 'shown';
        },
        hide: function(hideAll = 0) {
          const bg = '.modal-background';
          tl.to(bg, 0.4, {
            opacity: 0,
            onStart: function() {
              $('.header').removeClass('modal-bg-open');
            },
            onComplete: function() {
              $(bg).removeClass('active');
            },
          });
          this.state = 'hidden';
        },
        state: 'hidden',
      };
      this.location = {
        show: function() {
          th.hideAll();
          const $modalLoc = $('.modal-locations');
          const $hLoc = $('.header__location');
          const l = $hLoc.offset().left;
          const h = $hLoc.innerHeight();
          const top = h;

          th.bg.show();

          $modalLoc.css({
            top,
            left: l,
          });
          $modalLoc.addClass('active');
          tl.fromTo($modalLoc, 0.4, {
            y: -30,
            opacity: 0,
          }, {
            y: 0,
            opacity: 1,
            onStart: function() {
              $('.header').addClass('modal-bg-open');
            },
          }, '-=.4');
          this.state = 'shown';
        },
        hide: function(hideAll = 0) {
          const $modalLoc = $('.modal-locations');
          $('.header__location').removeClass('active');

          if (!hideAll) {
            th.bg.hide();
          }

          tl.to($modalLoc, 0.4, {
            y: -30,
            opacity: 0,
            onComplete: function() {
              $modalLoc.removeClass('active');
              $('.header').removeClass('modal-bg-open');
            },
          }, '-=.4');
          this.state = 'hidden';
        },
        state: 'hidden',
      };
      this.search = {
        show: function() {
          th.hideAll();
          th.bg.show();

          const hSearch = '.header__search';
          const hCont = '.header__content';
          tl
            .to(hCont, 0.2, {
              opacity: 0,
              onComplete: function() {
                $(hCont).hide();
                $(hSearch).css({
                  display: 'flex',
                });
              },
            }, '-=.3')
            .to(hSearch, 0.2, {
              opacity: 1,
              onComplete: function() {
                setTimeout(() => {
                  $(hSearch).addClass('active');
                  $('.header__search__input').select();
                }, 10);
              },
            }, '-=.3');

          this.state = 'shown';
        },
        hide: function(hideAll = 0) {
          th.bg.hide();
          const hSearch = '.header__search';
          const hCont = '.header__content';

          tl
            .to(hSearch, 0.2, {
              opacity: 0,
              onComplete: function() {
                $(hCont).show();
                $(hSearch).removeClass('active');
                $(hSearch).hide();
              },
            }, '-=.3')
            .to(hCont, 0.2, {
              opacity: 1,
            }, '-=.3');
          $(hSearch).removeClass('active');

          setTimeout(() => {
            $(hSearch).hide();
            $('.header__content').show();
            this.state = 'hidden';
          });
          this.state = 'hidden';
        },
        state: 'hidden',
      };
      this.menu = {
        show: function() {
          th.hideAll(1);
          $modalMenu.css({
            display: 'flex',
          });

          tl
            .add(function() {
              $('.header').addClass('modal-open');
            })
            .fromTo($modalMenu, 0.4, {
              clip: `rect(0px ${ww}px 0px ${ww}px)`,
              opacity: 0,
            }, {
              clip: `rect(0px ${ww}px ${wh}px 0px)`,
              opacity: 1,
              onComplete: function() {
                openModal();
                $modalMenu.css({
                  clip: 'auto',
                  display: 'flex',
                });
              },
            })
            .fromTo('.header__nav', 0.4, {
              opacity: 1,
            }, {
              opacity: 0,
            }, '-=.4')
            .fromTo('.modal-menu__scroller', 0.4, {
              opacity: 0,
              y: '30px',
            }, {
              opacity: 1,
              y: 0,
              onComplete: function() {
                $modalMenu.addClass('active');
                $('.modal-menu__scroller').removeAttr('style');
              },
            });
          this.state = 'shown';


        },
        hide: function(hide) {
          $('.header__hamburger').removeClass('active');
          hideModal();
          tl
            .fromTo($modalMenu, 0.4, {
              clip: `rect(0px ${ww}px ${wh}px 0px)`,
              opacity: 1,
            }, {
              clip: `rect(0px ${ww}px 0px ${ww}px)`,
              opacity: 0,
              onComplete: function() {
                $modalMenu.css({
                  clip: 'none',
                  display: 'none',
                });
              },
            })
            .fromTo('.header__nav', 0.4, {
              opacity: 0,
            }, {
              opacity: 1,
              onComplete: function() {
                $modalMenu.addClass('active');
                $('.modal-menu__scroller').removeAttr('style');
              },
            }, '-=.4');
          $('.header').removeClass('modal-open');
          this.state = 'hidden';

        },
        state: 'hidden',
      };
      this.hideAll = function(m = 0) {
        const modals = ['location', 'search', 'menu', 'navLinks'];
        modals.map(function(k) {
          if (th[k].state === 'shown') {
            var n;
            n = 1;
            if (m) {
              n = 0;
            }
            th[k].hide(n);
          }
        });
        // $('.modal').removeAttr('style').removeClass('active');

      };
      this.navLinks = {
        show: function() {
          th.bg.show();
          $('.site-nav__links').addClass('active');
          this.state = 'shown';
        },
        hide: function() {
          th.bg.hide();
          $('.site-nav__links').removeClass('active');
          this.state = 'hidden';
        },
        state: 'hidden',
      };
    }
  }

  const canClickCheck = () => {
    canClick = 0;
    setTimeout(function() {
      canClick = 1;
    }, 400);
  };

  const modals = new Modals();
  // modals.hideAll();

  $('.header__hamburger').on('click', function() {
    if (!canClick) return;
    canClickCheck();

    const $this = $(this);
    $this.toggleClass('active');

    if ($this.hasClass('active')) {
      modals.menu.show();
    } else {
      modals.menu.hide();
    }
  });


  // modals.show();
  $('.header__location').on('click', function() {

    if (!canClick) return;
    canClickCheck();

    const $this = $(this);

    $this.toggleClass('active');

    if ($this.hasClass('active')) {
      modals.location.show();
    } else {
      modals.location.hide();
    }
  });

  const activeLocation = () => {
    const loc = $('.header__location__name').text();
    $('.modal-locations').find('.h-loc').each(function() {
      const $this = $(this);
      const text = $this.text();
      if (loc === text) {
        $this.addClass('active');
      } else {
        $this.removeClass('active');
      }
    });
  };
  const locationCookie = () => {
    const name = 'location';
    var value = '; ' + document.cookie;
    var parts = value.split('; ' + name + '=');
    if (parts.length === 2) {
      const r = parts.pop().split(';').shift();
      $('.header__location__name').text(r);
    }
    activeLocation();
  };

  locationCookie();
  $('.h-loc').on('click', function() {
    const $this = $(this);
    if (!$this.hasClass('active')) {
      const text = $this.text();
      $('.header__location__name').text(text);
      document.cookie = `location=${text}`;
      modals.location.hide();
      activeLocation();
    }
  });

  $('.header__search-btn').on('click', function() {
    if (!canClick) return;
    canClickCheck();
    const $this = $(this);
    modals.search.show();
  });
  $('.header__search__close').on('click', function() {
    if (!canClick) return;
    canClickCheck();
    modals.search.hide();
  });
  $('.modal-background').on('click', function() {
    modals.hideAll(1);
  });
  var prevClassName;
  $('.site-nav__links').hover(function() {
    if (!isMobile) {
      if (!canClick) return;
      modals.navLinks.show();
      prevClassName = $('.header').attr('class').replace('header ', '');
      $('.header').removeClass(prevClassName).addClass('in-dark');
    }
  }, function() {
    if (!canClick) return;
    tl.clear();
    modals.navLinks.hide();
    $('.header').removeClass('in-dark').addClass(prevClassName);
  });
  $('.site-nav__links').on('click', function(e) {
    if (isMobile) {
      const $this = $(this);

      $this.toggleClass('active');
      if (ww < 1025) {
        if ($this.hasClass('active')) {
          modals.navLinks.show();
          prevClassName = $('.header').attr('class').replace('header ', '');
          $('.header').removeClass(prevClassName).addClass('in-dark');
        } else {
          modals.navLinks.hide();
          $('.header').removeClass('in-dark').addClass(prevClassName);
        }

      }
      if ($this.find('a').eq(0).is(e.target)) {
        return false;
      }
    }
  });
  $('.main__objects__review').on('click', function(e) {
    tl.fromTo('.main__review', 0.4, {
      opacity: 0,
      x: '100%',
      display: 'flex',
    }, {
      opacity: 1,
      x: '0%',
      onComplete: function() {
        $('.main__review').addClass('active').removeAttr('style');
        openModal();
      },
    });
  });

  $('.main__review').find('.close').on('click', function() {
    hideModal();
    tl.to('.main__review', 0.4, {
      opacity: 0,
      x: '100%',
      onComplete: function() {
        $('.main__review').removeClass('active').removeAttr('style');
      },
    });
  });
}
