import $ from 'jquery';

export default function scrollParallax() {

  global.scroll = $(window).scrollTop();

  const ObjToArr = (obj) => {
    var arr = Object.keys(obj).map(function(k) {
      return obj[k];
    });
    return arr;
  };

  (function($) {
    $.fn.prlxShow = function() {
      const prlx = () => {
        this.each(function() {
          const $this = $(this);
          const top = $this.offset().top;

          if (!$this.hasClass('init-parallax')) {
            $this.addClass('init-parallax').addClass('hide');
          }
          if (scroll + wh / 2 > top) {
            $this.addClass('show').removeClass('hide');
          }
        });
      };
      return prlx();
    };


    $.fn.parallax = function(speed = 1, options, heightScale = 1) {
      const settings = $.extend({}, options);
      return this.each(function() {
        const $this = $(this);
        const top = Math.floor($this.offset().top);
        const h = Math.floor($this.innerHeight() * heightScale);

        if (scroll + wh >= top && scroll <= top + h) {

          var initState = [];
          var type = [];

          const changeVal = (type) => {
            const scale = (h / wh);

            if (Object.keys(initState).indexOf(type) > -1) {
              const isInt = (number) => {
                return Number.isInteger(Math.floor(number));
              };

              const arr = initState[type];
              const val0 = isInt(arr[0]) ? arr[0] : arr[0].replace('%', '') / 1;
              const val1 = isInt(arr[1]) ? arr[1] : arr[1].replace('%', '') / 1;
              const length = val1 - val0;
              const enlarge = wh / (length * 1.4);
              const prepVal = ((wh + scroll - top + val0 * enlarge) / enlarge * speed).toFixed(3) / 1;

              var resultValue;
              if (val1 > val0) {
                resultValue = Math.min(val1, Math.max(prepVal, val0));

                if (!isInt(arr[0]) || !isInt(arr[1])) {
                  resultValue += '%';
                }
                return resultValue;
              } else {
                resultValue = Math.min(val0, Math.max(prepVal, val1));
                if (!isInt(arr[0]) || !isInt(arr[1])) {
                  resultValue += '%';
                }
                return resultValue;
              }
            } else {
              return null;
            }
          };

          let arr = ObjToArr(settings);
          arr.map(function(k, i) {
            const keyName = Object.keys(k);
            const keyValue = Object.values(k);


            keyName.map((kN, j) => {
              const kV = keyValue[j];

              if (i === 0) {
                initState[kN] = [];
                type.push(kN);
              }
              initState[kN].push(kV);
            });
          });

          $this.addClass('init');

          const opacity = changeVal('opacity');
          var x = changeVal('x') || 0;
          var y = changeVal('y') || 0;
          var flex = changeVal('flex') || null;

          if (x === 0 && y === 0) {
            x = null;
            y = null;
          }


          $this.css({
            transform: `translate(${x}px ,${y}px)`,
            opacity: opacity,
            flexBasis: flex,
          });
        }
      });
    };
  }(jQuery));

  const rolls = () => {
    $('.parallax-transfusion').each(function() {
      const $this = $(this);
      const top = Math.floor($this.offset().top);
      const h = Math.floor($this.innerHeight());

      const $img = $this.find('.parallax-transfusion-imgs').children('img');
      const img_l = $img.length;

      if (scroll + wh >= top && scroll <= top + h) {
        const $this = $(this);
        const scrTop = scroll - top + wh;
        const length = wh + h;

        const percent = (length - scrTop) / (wh + h) * 1.25;
        const num = Math.floor(img_l - (img_l * percent));
        const opacity = ((img_l - num) - percent * img_l).toFixed(3);


        $img.eq(num + 1).siblings().removeClass('show').removeAttr('style');
        $img.eq(num).css({ opacity: 1 });
        $img.eq(num + 1).css({
          opacity: opacity,
        }).addClass('show');
        // ПРОТЕСТИТЬ НА ПЛАВНОСТЬ
      }
    });
  };


  const parallaxes = () => {
    $('.pic-cover__info').prlxShow();
    $('.graphic-growth').prlxShow();
    if (ww > 767) {
      $('.look-news').find('.video').parallax(1, {
        from: {
          opacity: 0,
          x: -500,
        },
        to: {
          opacity: 1,
          x: 0,
        },
      });
    } else {
      $('.look-news').find('.video').removeAttr('style');
    }
    $('.section').children('img').parallax(1, {
      from: {
        opacity: 0,
      },
      to: {
        opacity: 1,
      },
    });
    if (ww > 767) {
      $('.circle').parallax(0.39, {
        from: {
          opacity: 0,
          y: 300,
        },
        to: {
          opacity: 1,
          y: -400,
        },
      }, 10);
    } else {
      $('.circle').parallax(0.45, {
        from: {
          opacity: 0,
          y: 100,
        },
        to: {
          opacity: 1,
          y: -100,
        },
      }, 10);
    }

    if (ww > 1024) {
      $('.parallax-circles__title').parallax(0.7, {
        from: {
          y: 300,
        },
        to: {
          y: 0,
        },
      });
      // $('.parallax-circles__descr').parallax(0.7, {
      //   from: {
      //     y: 200,
      //   },
      //   to: {
      //     y: 0,
      //   }
      // });
    } else {
      $('.parallax-circles__title').removeAttr('style');
    }

    if (ww > 767) {
      $('.img-parallax-sizing__item').eq(0).parallax(0.5, {
        from: {
          flex: '20%',
        },
        to: {
          flex: '300%',
        },
      });
    }

    rolls();
  };
  parallaxes();
  $(window).on('scroll', function(e) {
    scroll = $(window).scrollTop();
    parallaxes();
  });
}
