import $ from 'jquery';

export default function formValidation() {

  const $inputPhone = $('.input-phone');

  var formValidArr = [];
  var timeToShow = 0;

  const checkAllInputs = ($form) => {
    const $input_textarea = $form.find('[type="text"], textarea');
    $input_textarea.each(function() {
      const $this = $(this);

      if (!$this.hasClass('success')) {
        return false;
      }
    });
  };


  const hideValidMessage = () => {
    const $checkValid = $('.input__check-valid-message');
    $checkValid.css({
      opacity: 0,
      transform: 'translate(25px, -50%)'
    });
    timeToShow = 300;
    setTimeout(() => {
      $checkValid.remove();
    }, 300);
  };

  const showValidMessage = ($this, text) => {
    const t = $this.offset().top;
    const l = $this.offset().left;
    const w = $this.innerWidth();
    const h = $this.innerHeight();
    var left = l + w + 8;
    var top = t + h / 2;
    var right = 'auto';
    var translate = 'translate(0px, -50%)';

    const condition = ($checkValid) => {
      $checkValid.removeClass('arrow-bot');
      if (left + $checkValid.innerWidth() > ww) {
        left = 'auto';
        top = t + h + 20;
        right = ww - (l + w);
        translate = 'translate(0px, 0)';
        $checkValid.addClass('arrow-bot');
      } else {
        translate = 'translate(0px, -50%)';
        $checkValid.removeClass('arrow-bot');
      }
    };

    setTimeout(() => {
      if (!$('.input__check-valid-message').length) {
        $('body').append(`<div class="input__check-valid-message">${text}</div>`);
        const $checkValid = $('.input__check-valid-message');

        condition($checkValid);
        $checkValid.css({top, left, right});
        $checkValid.css({
          transition: '.3s',
        });
        setTimeout(() => {
          $checkValid.css({
            opacity: 1,
            transform: translate
          });
        });
      } else {
        const $checkValid = $('.input__check-valid-message');

        condition($checkValid);

        $checkValid.css({top, left, right});
        setTimeout(() => {
          $checkValid.css({
            opacity: 1,
            transform: translate
          });
        });
      }
      timeToShow = 0;
    }, timeToShow);
  };


  const formIsValid = (i) => {
    return formValidArr[i].valid.reduce(function(p, n) {
      return p && n;
    });
  };

  $inputPhone.mask('+7 (999) 999 99 99', {
    placeholder: '_'
  });
  $inputPhone.focusout(function() {
    var $this = $(this);
    if ($this.val() === '+7 (___) ___ __ __') {
      $this.val('');
    }
  });


  $('[name="name"]').on(' keypress keyup', function(e) {
    if (e.originalEvent.key != null) {
      if (!e.originalEvent.key.match(/^[a-zA-Zа-яА-Я\s]+$/)) {
        return false;
      }
    }
  });

  $('form').on('submit', function(e) {
    const $this = $(this);
    const i = $('form').index(this);
    const $btn = $this.find('.btn');
    const btnText = $btn.text();
    checkAllInputs($this);
    if (!formIsValid(i)) {
      e.preventDefault();
    } else {
      const $input_textarea = $this.find('.input, .textarea');
      $input_textarea.blur();
      $input_textarea.val('');
      $btn.text('Отправка');
      $btn.attr('disabled', true);
      $this.find('.input__check-valid').removeClass('success').removeClass('error');
      setTimeout(() => {
        alert('Отправка формы ' + i + 1);
        $btn.attr('disabled', false);
        if ($this.hasClass('form')) {
          $btn.addClass('unsuccess');
        }
        $btn.removeClass('success');
        $btn.text(btnText);
      }, 1000);
    }
    return false;
  });
  $('form').each(function(e) {
    const $this = $(this);
    const $form = $this;
    const $input_textarea = $this.find('[type="text"], textarea');
    const $btn = $this.find('.btn');
    $this.attr('novalidate', true);

    const formNum = e;
    const arrInput = [];

    if ($form.hasClass('form')) {
      $btn.addClass('unsuccess');
    }

    $btn.on('click', function() {
      $input_textarea.each(function() {
        $input_textarea.each(function() {
          const $this = $(this);
          const i = $input_textarea.index(this);

          checkForm_vals(i, $this);

          if (!$this.hasClass('success')) {
            $form.find('.input__check-valid').eq(i).addClass('error');
            $this.focus();
            return false;
          }
        });
      });
    });

    $input_textarea.on(' keypress keyup', function(e) {
      const eKey = e.keyCode || e.which;
      const $this = $(this);
      const i = $input_textarea.index(this);
      checkForm_vals(i, $this);

      if (eKey === 13) {
        if (!formIsValid(formNum)) {
          e.preventDefault();
        }
      }


      if (formIsValid(formNum)) {
        $btn.removeClass('unsuccess').addClass('success');
      } else {
        if ($form.hasClass('form')) {
          $btn.removeClass('success').addClass('unsuccess');
        }
      }


    });
    $input_textarea.on('keydown', function(e) {
      const eKey = e.keyCode || e.which;
      const $this = $(this);
      const i = $input_textarea.index(this);

      if (eKey === 13) {
        const text = checkForm_vals(i, $this);

        const $form = $this.parents('form');
        const $input_textarea = $form.find('[type="text"], textarea');

        if ($this.hasClass('success')) {
          $input_textarea.each(function() {
            const $this = $(this);
            if (!$this.hasClass('success')) {
              $this.focus();
              return false;
            }
            hideValidMessage();
          });
        }
        if ($this.hasClass('error')) {
          showValidMessage($this, text);
          const $chValid = $('form').eq(formNum).find('.input__check-valid').eq(i);
          $chValid.removeClass('success').addClass('error');
        }
      }

    });

    $input_textarea.on('focusout', function() {
      const $this = $(this);
      const i = $input_textarea.index(this);

      const text = checkForm_vals(i, $this);

      if ($this.hasClass('error')) {
        showValidMessage($this, text);
        const $chValid = $('form').eq(formNum).find('.input__check-valid').eq(i);
        $chValid.removeClass('success').addClass('error');
      }

      // hideValidMessage();
    });

    $input_textarea.on('focus', function() {
      const $this = $(this);
      const i = $input_textarea.index(this);


      if ($this.hasClass('success')) {
        checkForm_vals(i, $this);
      }

      if ($this.hasClass('error')) {
        const text = checkForm_vals(i, $this);
        showValidMessage($this, text, i);
      }
    });
    $this.find('[type="text"], textarea').each(function() {
      const $this = $(this);
      if (!$this.attr('disabled') && $this.prop('required')) {
        arrInput.push(false);
      }
    });

    formValidArr.push({
      id: 0,
      valid: arrInput
    });
  });

  function checkForm_vals(e, $this) {


    var t_l = $this.val().length;
    var text;
    var isValid = false;

    const formNum = $this.parents('form').index('form');

    const inputName = (name) => {
      return $this.filter('[name="' + name + '"]').length;
    };

    if ($this.prop('required')) {
      if (!t_l) {
        text = 'Вы не можете оставить поле пустым';
      } else {

        if (inputName('name')) {
          isValid = $this.val().match(/^[a-zA-Zа-яА-Я\s]+$/);
        }
        else if (inputName('email')) {
          isValid = $this.val().match(/^[0-9a-zA-Z\-_\.]+@[0-9a-zA-Z\-_\.]+\.[a-zA-Z]+$/);
          text = 'Введите корректную почту';
        }
        else if (inputName('message')) {
          isValid = true;
        }
        else if ($this.filter('[name="password"]').length) {
          isValid = (t_l < 8 && t_l > 0);
          text = 'Допустимо минимум 8 символов';
        }
        else if ($this.hasClass('input-phone')) {
          isValid = $this.val().match(/^[\+]?[0-9]?[-\s\.]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{2}[-\s\.]?[0-9]{2}$/);
          text = 'Заполните поле полностью';
        } else {
          isValid = $this.length;
        }
        if ($this.attr('disabled') && !$this.attr('required')) {
          isValid = true;
        }
      }
    } else {
      isValid = true;
    }
    const $chValid = $('form').eq(formNum).find('.input__check-valid').eq(e);
    if (isValid) {
      if ($this.attr('required')) {
        $this.removeClass('error').addClass('success');
        $chValid.removeClass('error').addClass('success');
        hideValidMessage();
        formValidArr[formNum].valid[e] = true;
      }
    } else if (isValid === null || !isValid) {
      $this.removeClass('success').addClass('error');
      formValidArr[formNum].valid[e] = false;
    }
    $('.input__check-valid-message').text(text);
    return text;

  }

  $('.input__check-valid').on('click', function() {
    const $this = $(this);

    if ($this.hasClass('error')) {
      $this.siblings('input').val('').focus();
    }
  });

}
