import $ from 'jquery';

export default function videoResponsive() {
  const $videoWrapper = $('.video-wrapper');
  var changeDOM = [];

  $videoWrapper.each(function() {
    changeDOM.push(null);
  });

  $videoWrapper.each(function(e) {
    const $this = $(this);
    const $title = $this.find('.video__title');
    const $info = $this.find('.video__info');

    if (ww < 768) {
      if (changeDOM[e] !== 0) {
        $title.prependTo($this);
        changeDOM[e] = 0;
      }
    } else {
      if (changeDOM[e] !== 1) {
        changeDOM[e] = 1;
        $title.prependTo($info);
      }
    }
  });
}
