import $ from 'jquery';

export default function colorMap() {
  const map = [
    {
      title: 'RAL 9017',
      texture: '#404040',
      panel: 'img/color-map/black.png'
    },
    {
      title: 'RAL 2007',
      texture: '#FF7727',
      panel: 'img/color-map/orange.png'
    },
    {
      title: 'Дерево',
      texture: 'img/color-map/bg-wood.jpg',
      panel: 'img/color-map/wood.png'
    },
    {
      title: 'RAL 2007',
      texture: '#2832c2',
      panel: 'img/color-map/blue.png'
    },
    {
      title: 'RAL 5021',
      texture: '#ffb200',
      panel: 'img/color-map/yellow.png'
    },
    {
      title: 'RAL 5005',
      texture: '#94989b',
      panel: 'img/color-map/gray.png'
    },
    {
      title: 'RAL 6005',
      texture: '#355147',
      panel: 'img/color-map/green.png'
    }
  ];
  var count = 0;
  const init = () => {
    $('.color-map__items').each(function() {
      const $this = $(this);

      const arrMap = Object.entries(map);
      const DOM = (k, i) => {
        var back = '', backImg = '';
        if (k[1].texture.indexOf('#') === 0) {
          back = `style="background-color: ${k[1].texture};"`;
        } else {
          backImg = `style="background-image: url(${k[1].texture});"`;
        }
        return `
        <div class="color-map__item${i === 0 ? ' main' : ''}" ${back}>
          <div class="color-map__item__texture" ${backImg}></div>
          <div class="color-map__item__title">
            ${k[1].title}
          </div>
        </div>
      `;
      };

      arrMap.map((k, i) => {
        if (i < 5) {
          $this.append(DOM(k, i));
          $('.color-map__item').eq(i).data('state', {index: i});
        }
        $('.color-map__item.main').append(`
          <div class="color-map__item__img">
            <div class="color-map__item__img-inner">
              <img src="${k[1].panel}">
            </div>
          </div>
        `);
      });
      $('.color-map__item__img').eq(0).addClass('active');
    });
  };
  init();

  const sliding = (count) => {
    const arrMap = Object.entries(map);
    arrMap.map((k, i) => {

      var num;
      var index;

      num = i - count;
      index = i;

      if (num < 0) {
        num += map.length;
      }

      const $item = $('.color-map__item').eq(num);
      // $item.data('state').num = i;
      if ($item.length > 0) {
        // if (index > map.length - 2) {
        //   index = 0;
        // }
        $item.data('state').index = index;
      }

      const $texture = $item.find('.color-map__item__texture');
      const $panel = $('.color-map__item__img').eq(count);


      if (k[1].texture.indexOf('#') === 0) {
        $item.css({
          backgroundColor: k[1].texture,
          backgroundImage: ''
        });
        $texture.css({
          opacity: 1,
        });
        $texture.css({
          opacity: 0,
        });
      } else {
        $texture.css({
          opacity: 0,
        });
        $texture.css({
          backgroundImage: `url(${k[1].texture})`,
          opacity: 1
        });
      }
      $panel.siblings().removeClass('active');
      $panel.addClass('active');
      $item.find('.color-map__item__title').text(k[1].title);
      // $item.find('img').attr('src', k[1].panel);

    });
  };
  $('.color-map').each(function() {
    const $this = $(this);

    $this.find($('.slider__next, .slider__prev')).on('click', function() {
      const $this = $(this);

      if ($this.hasClass('slider__next')) {
        count += 1;
        if (count > map.length - 1) {
          count = 0;
        }
      } else {
        count -= 1;
        if (count < 0) {
          count = map.length - 1;
        }
      }
      sliding(count);
    });
  });
  $('.color-map__item').on('click', function() {
    const $this = $(this);


    if (!$this.hasClass('main')) {
      count = $this.data('state').index;
      sliding(count);
    }
  });
}
